import React, { useState } from 'react';
import { Text } from '@sitecore-jss/sitecore-jss-react';
import get from 'lodash/get';
import Slider from 'react-slick';
import { useTheme } from '@material-ui/core/styles';

import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import useStyles from './style';
import Typography from '../../../components/atoms/Typography';
import Container from '../../../components/molecules/Container';
import { COMPONENT_NAME } from './locators';
import QuoteIcon from '../../../../styles/svgIcons/QuoteIcon';
import Grid from '../../../components/molecules/Grid';
import { settings } from './config';
import { getPageName, getComponentName } from '../../../utils/getPageComponentInfo';
import { pushAnaylyticsData, createDynamicLabels } from '../../../../utils/analytics';
import { analyticsData } from '../../../../constants/Analytics/analytics-data';
import '../../../../styles/themes/slick-themes.css';
import 'slick-carousel/slick/slick.css';
import clsx from 'clsx';

const QuotesCarousel = (props) => {
  const pageName = getPageName(props);
  const componentName = getComponentName(props);
  const [currentPageNumber, setPageNumber] = useState(1);
  const quotesList = get(props, 'fields.quotes', []);
  const title = get(props, 'fields.title', '');
  const theme = useTheme();
  const stripColor = get(props, 'fields.stripColor.fields.colorCode.value', '');
  const classes = useStyles({ stripColor });
  const shouldComponentRender = () => {
    return (quotesList && quotesList.length > 0) || false;
  }

  const addAnalyticsInfo = () => {
    const obj = {
      ...analyticsData.pageBodyInteraction.swipeArrowClick,
      ...{ label: createDynamicLabels([pageName, componentName, title && title.value]) },
    };
    pushAnaylyticsData(obj);
  };

  settings.rtl = false;
  settings.initialSlide = theme && theme.direction === 'rtl' ? quotesList && quotesList.length - 1 : 0;
  const isRTL = theme && theme.direction === 'rtl';

  return shouldComponentRender() ? (
    <div>
      <Container className={classes.root} data-locator={COMPONENT_NAME}>
        <Grid className={clsx(classes.gridContainer, isRTL && classes.rtlClass)}>
          {
            title && title.value &&
            <Typography variant="h4" component="h2" className={classes.title}>
              <Text field={title} />
            </Typography>
          }
          <Slider
            beforeChange={(currentSlide, nextSlide) => {
              setTimeout(() => {
                setPageNumber(nextSlide + 1);
                addAnalyticsInfo();
              }, 0);
            }}
            {...settings}
          >
            {quotesList.map((item, index) => {
              const description = get(item, 'fields.description', '');
              const authorName = get(item, 'fields.authorName', '');
              const authorDesignation = get(item, 'fields.authorDesignation', '');
              return (
                <div className='quotesCarousel' key={index}>
                  {
                    description && description.value &&
                    <Typography variant="h5" component="p" className={classes.description}>
                      <i className={classes.quoteIcon}>{QuoteIcon}</i>
                      <Text field={description} />
                    </Typography>
                  }
                  {
                    authorName && authorName.value &&
                    <Typography variant="body1Bold" component="p" className={classes.author}>
                      <Text field={authorName} />
                    </Typography>
                  }
                  {
                    authorDesignation && authorDesignation.value &&
                    <Typography variant="body2" component="p" className={classes.authorDesignation}>
                      <Text field={authorDesignation} />
                    </Typography>
                  }
                </div>
              )
            })}
          </Slider>
          <Typography variant="body1" component="p">
            <span className={classes.pageInfo}>{currentPageNumber}</span> / <span className={classes.totalPages}>{quotesList.length}</span>
          </Typography>
        </Grid>
      </Container>
    </div>
  ) : null;
}

export default withErrorBoundary(QuotesCarousel);
