import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => ({
  root: {
    paddingTop: theme.spacing(6),
    paddingBottom: theme.spacing(8),
    ...theme.shape.componentSpacing,
    [theme.breakpoints.up('lg')]: {
      paddingBottom: theme.spacing(6),
    },
    width: '100%',
    '& .slick-list': {
      padding: '0 !important',
      '& .quotesCarousel': {
        '& p, & h2': {
          textAlign: theme.mixins.textLeft(),
        },
        [theme.breakpoints.up('lg')]: {
          maxWidth: '88%',
        },
      },
    },
    '& .slick-slide': {
      cursor: 'pointer',
      transition: 'box-shadow .3s, background-color .3s',
      [theme.breakpoints.up('lg')]: {
        width: '60%',
      },
    },
    '& .slick-disabled': {
      opacity: theme.palette.action.disabledOpacity,
      pointerEvents: 'none',
    },
    '& .slick-prev, & .slick-next': {
      zIndex: '100',
      width: 'auto',
      height: 'auto',
      transition: 'opacity 0.5s ease-out',
      top: '98%',
      [theme.breakpoints.up('lg')]: {
        top: pxToRem(-132),
      },
    },
    '& .slick-prev > button, & .slick-next > button': {
      border: `1px solid ${theme.mixins.Black()}`,
      width: pxToRem(46),
      height: pxToRem(46),
      transition: 'all 0.3s ease-in-out',
      [theme.breakpoints.up('lg')]: {
        border: '1px solid #000',
        width: pxToRem(84),
        height: pxToRem(84),
      },
      '& svg': {
        [theme.breakpoints.down('md')]: {
          width: theme.spacing(2),
          height: theme.spacing(2),
        },
      },
      '&:hover': {
        backgroundColor: theme.mixins.Black(300),
      },
    },
    '& .MuiIconButton-label': {
      color: theme.mixins.Black(),
      [theme.breakpoints.up('lg')]: {
        color: theme.mixins.Black(),
      },
    },
    '& .slick-prev::before,& .slick-next::before': {
      content: "''",
    },
    '& .slick-prev': {
      bottom: 0,
      ...theme.mixins.left('auto'),
      transform: 'none',
      ...theme.mixins.right(pxToRem(64)),
      [theme.breakpoints.up('lg')]: {
        top: 0,
        ...theme.mixins.right(pxToRem(235)),
      },
    },
    '& .slick-next': {
      bottom: 0,
      ...theme.mixins.right(0),
      transform: 'none',
      [theme.breakpoints.up('lg')]: {
        top: 0,
        ...theme.mixins.right(pxToRem(115)),
      },
    },

    [theme.breakpoints.up('lg')]: {
      '&::after': {
        content: '""',
        position: 'absolute',
        width: pxToRem(92),
        height: '100%',
        backgroundColor: ({ stripColor }) => stripColor,
        ...theme.mixins.right('0'),
        top: 0,
      },
      paddingTop: theme.spacing(11),
      paddingBottom: theme.spacing(11),
    },
  },
  gridContainer: {
    [theme.breakpoints.down('md')]: {
      '&::after': {
        content: '""',
        position: 'absolute',
        width: '100%',
        height: theme.spacing(4),
        backgroundColor: ({ stripColor }) => stripColor,
        ...theme.mixins.right('0'),
        left: 0,
        bottom: 0,
      },
    },
  },
  title: {
    paddingBottom: pxToRem(40),
    [theme.breakpoints.up('lg')]: {
      paddingBottom: pxToRem(80),
    },
  },
  description: {
    color: theme.palette.text.darkGrey,
    paddingBottom: pxToRem(44),
    [theme.breakpoints.up('lg')]: {
      paddingBottom: pxToRem(77),
    },
  },
  author: {
    [theme.breakpoints.up('lg')]: {
      paddingBottom: pxToRem(8),
    },
  },
  authorDesignation: {
    paddingBottom: theme.spacing(4),
    [theme.breakpoints.up('lg')]: {
      paddingBottom: theme.spacing(5),
    },
  },
  quoteIcon: {
    display: 'block',
    '& svg': {
      height: pxToRem(40),
      width: pxToRem(54),
      [theme.breakpoints.up('lg')]: {
        height: pxToRem(48),
        width: pxToRem(65),
      },
    },
  },
  pageInfo: {
    color: theme.mixins.AbuDhabiRed(),
  },
  totalPages: {
    color: theme.mixins.Grey(),
  },
  rtlClass: {
    '&  [dir="ltr"] .slick-prev::before,& [dir="ltr"] .slick-next::before': {
      content: "''",
    },
    '& .slick-list .quotesCarousel': {
      [theme.breakpoints.up('lg')]: {
        position: 'relative',
        right: '-12%',
      },
    },
    '& [dir="ltr"] .slick-prev': {
      ...theme.mixins.left('auto'),
      ...theme.mixins.right('0'),
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.right(pxToRem(115)),
      },
    },
    '& [dir="ltr"] .slick-next': {
      ...theme.mixins.right('17.5%'),
      ...theme.mixins.left('auto'),
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.right('8.5%'),
        ...theme.mixins.right(pxToRem(235)),
      },
    },
  },
}));

export default useStyles;
